<template>
  <v-container style="margin-top: 150px; margin-bottom: 150px; max-width: 800px;">
    <v-layout align-center mb-5>
      <v-icon small>keyboard_backspace</v-icon>
      <router-link to="/blogg" class="body-2">
        Tilbake
      </router-link>
    </v-layout>

    <h1 v-html="post.title.rendered"></h1>

    <v-layout justify-center>
      <v-flex v-html="post.content.rendered" style="line-height: 1.8em;"></v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      post: {
        title: {
          rendered: ''
        },
        content: {
          rendered: ''
        }
      }
    }
  },

  created () {
    this.$axios.get('/posts/' + this.$route.params.id + '?_embedd').then((items) => {
      console.log(items.data)
      this.post = items.data
    })
  }
}
</script>

